* {
  font-family: 'Montserrat', sans-serif;
  /*font-family: 'Roboto', sans-serif;*/
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.wrapper {
  max-width: 940px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.red-btn {
  background-color: #FF4E50;
  border-radius: 40px;
  font-size: 14px;
  color: #fff;
  font-weight: bold;
  padding: 12px 20px;
}

/* Header */

.header {
  background: url("../img/background.png") center no-repeat;
  background-size: cover;
}

.header .top-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 70px;
}

.header .top-header .logo-header {
  color: #fff;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: bold;
}

.header .top-header .auth {
  display: flex;
  flex-wrap: wrap;
}

.header .top-header .auth li {
  margin-left: 5px;
}

.header .top-header .auth__item {
  display: block;
  color: #fff;
  font-size: 14px;
  padding: 10px 18px;
}

.header .top-header .auth__item:hover,
.header .top-header .auth__item:focus {
  border: 2px solid #fff;
  border-radius: 40px;
  margin: -2px;
}

.header .top-header .auth__item_active {
  border: 2px solid #fff;
  border-radius: 40px;
  margin: -2px;
}

.header .middle-header {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(100% - 10px);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.header .middle-header__title {
  font-size: 52px;
  font-weight: bold;
  color: #fff;
  margin-bottom: 28px;
  margin-top: 148px;
  max-width: 470px;
}

.header .middle-header__text {
  display: block;
  font-family: 'Roboto', sans-serif;
  font-size: 22px;
  font-weight: 300;
  line-height: 24px;
  color: #F7F3F2;
  margin-bottom: 34px;
}

.header .middle-header_btn {
  display: block;
  margin-bottom: 410px;
}

/* -- Header */

/* Main */

.section-title {
  font-size: 22px;
  font-weight: bold;
  margin-top: 94px;
  margin-bottom: 35px;
  text-align: center;
}

/* Section How it Works */

.how-it-works .steps {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
}

.how-it-works .steps .step {
  width: 30.6%;
  height: 400px;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 25px 20px 25px;
  box-sizing: border-box;
  width: calc(33.333333333333336% - 10px);
}

.how-it-works .steps .step_one {
  background: url("../img/step1.png") center no-repeat;
}

.how-it-works .steps .step_two {
  background: url("../img/step2.png") center no-repeat;
}

.how-it-works .steps .step_three {
  background: url("../img/step3.png") center no-repeat;
}

.how-it-works .steps .step__label {
  display: block;
  padding: 4px 14px;
  text-transform: uppercase;
  font-size: 10px;
  letter-spacing: 2px;
  margin-bottom: 25px;
}

.how-it-works .steps .step__title {
  font-weight: bold;
  line-height: 14px;
  font-size: 24px;
  color: #fff;
  margin-bottom: 25px;
  width: 100%;
}

.how-it-works .steps .step__text {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  color: #E3E3E3;
  font-weight: 300;
  width: 100%;
}

/* -- Section How it Works */

/* Section People */

.people {
  text-align: center;
}

.people .people-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
  text-align: center;
}

.people .people-list__item {
  margin-bottom: 45px;
  width: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.people .people-list__item_photo-wrap {
  width: 120px;
  height: 120px;
  position: relative;
  margin-bottom: 14px;
}

.people .people-list__item_photo-wrap::after {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 4px solid #fff;
}

.people .people-list__item_photo-wrap-one::after {
  background: #FF4E50 url("../img/music-ico.png") center no-repeat;
}

.people .people-list__item_photo-wrap-two::after {
  background: #19D4CA url("../img/brash-ico.png") center no-repeat;
}

.people .people-list__item_photo-wrap-three::after {
  background: #FFA710 url("../img/camera-ico.png") center no-repeat;
}

.people .people-list__item_photo-wrap-four::after {
  background: #667DF3 url("../img/airplane-ico.png") center no-repeat;
}

.people .people-list__item_photo {
  border-radius: 50%;
}

.people .people-list__item_name {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 12px;
}

.people .people-list__item_text {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 14px;
  color: #666;
  width: 220px;
}

.people__btn {
  display: inline-block;
  background-color: transparent;
  color: #FF4E50;
  font-weight: bold;
  border: 2px solid #FF4E50;
  padding: 10px 22px;
}

/* -- Section People */

/* Section with activity and holiday */

.holiday .activities {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
}

.holiday .activities .activity {
  width: calc(32.5% - 10px);
  height: 310px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 20px;
  font-size: 22px;
  font-weight: bold;
  color: #fff;
  box-shadow: inset 0 0 300px 0 rgba(0, 0, 0, 0.6);
  border-radius: 4px;
}

.holiday .activities .activity_wide {
  width: calc(66.66666666666667% - 10px);
}

.holiday .activities .activity_one {
  background: url("../img/activity-1.png") center no-repeat;
}

.holiday .activities .activity_two {
  background: url("../img/activity-2.png") center no-repeat;
}

.holiday .activities .activity_three {
  background: url("../img/activity-3.png") center no-repeat;
}

.holiday .activities .activity_four {
  background: url("../img/activity-4.png") center no-repeat;
}

.holiday .activities .activity_five {
  background: url("../img/activity-5.png") center no-repeat;
}

.holiday .activities .activity_six {
  background: url("../img/activity-6.png") center no-repeat;
}

.holiday .activities .activity_seven {
  background: url("../img/activity-7.png") center no-repeat;
}

/* -- Section with activity and holiday */

/*Section with callback*/

.callback {
  text-align: center;
}

.callback__title {
  display: block;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  line-height: 14px;
  font-size: 18px;
  color: #666;
  margin-bottom: 20px;
}

.callback__form {
  margin-bottom: 100px;
}

.callback__input {
  font-family: 'Roboto', sans-serif;
  color: #999;
  font-weight: 300;
  font-size: 14px;
  width: 300px;
  background: #fff;
  border: 1px solid #D4D4D4;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.073313);
  border-radius: 3px;
  padding: 14px;
}

.callback__submit {
  display: inline-block;
  margin-left: 10px;
}

/* -- Section with callback*/

/* -- Main */

/* Footer */

.footer {
  border-top: 2px solid #FF4E50;
  padding: 36px 0;
}

.footer__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.footer__wrap .logo-footer {
  text-transform: uppercase;
  font-size: 16px;
  color: #FF4E50;
}

.footer__wrap .nav-footer__item {
  font-size: 14px;
  line-height: 26px;
  font-weight: 300;
}

.footer__wrap .nav-footer__item a {
  font-family: 'Roboto', sans-serif;
  color: #999;
}

.footer__wrap .nav-footer__item_title {
  font-weight: bold;
  line-height: normal;
  color: #444444;
  padding-bottom: 10px;
}

.footer__wrap .copyright {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.footer__wrap .copyright__text {
  font-size: 10px;
  font-weight: bold;
  letter-spacing: 1px;
  color: #ccc;
  text-transform: uppercase;
  padding-bottom: 6px;
}

/* -- Footer */

@media screen and (max-width: 990px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .people .people-list {
    justify-content: center;
  }

  .people .people-list__item {
    width: calc(50% - 10px);
  }

  .people .people-list__item {
    width: 350px;
  }
}

@media screen and (max-width: 748px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .how-it-works .steps {
    flex-direction: column;
    align-items: center;
  }

  .how-it-works .steps .step {
    width: 300px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .how-it-works .steps .step {
    height: 240px;
  }

  .holiday .activities {
    justify-content: space-around;
  }

  .holiday .activities .activity {
    width: calc(50% - 10px);
  }

  .holiday .activities .activity {
    height: 120px;
  }

  .callback__form {
    margin-bottom: 40px;
  }

  .callback__input {
    margin-bottom: 15px;
    width: 280px;
  }

  .callback__submit {
    width: 280px;
  }

  .footer__wrap {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer__wrap .logo-footer {
    margin-bottom: 15px;
    font-size: 36px;
  }

  .footer__wrap .nav-footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .footer__wrap .nav-footer__item {
    margin-bottom: 5px;
    font-size: 28px;
    margin-right: 20px;
  }

  .footer__wrap .nav-footer__item:last-child {
    margin-right: 0;
  }

  .footer__wrap .nav-footer__item_title {
    flex-basis: 100%;
    margin-top: 12px;
    margin-bottom: 12px;
    font-size: 30px;
    margin-right: 0;
  }

  .footer__wrap .copyright {
    align-items: center;
  }

  .footer__wrap .copyright__text {
    font-size: 14px;
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .header .top-header {
    flex-direction: column;
    padding-top: 30px;
  }

  .header .top-header .auth {
    order: -1;
    padding-bottom: 96px;
  }

  .header .middle-header__title {
    font-size: 30px;
    max-width: 272px;
  }

  .header .middle-header__text {
    font-size: 18px;
    max-width: 272px;
  }

  .header .middle-header_btn {
    padding: 12px 46px;
    margin-bottom: 152px;
  }

  .holiday .activities {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .holiday .activities .activity {
    width: calc(83.33333333333334% - 10px);
  }

  .callback__input {
    width: 240px;
  }

  .callback__submit {
    width: 240px;
  }
}